<template>
  <div>
    <div class="c-map-container col-12">
      <div class="c-pressure-map"></div>
    </div>
  </div>
</template>

<script>
import d3 from 'd3';

export default {
  methods: {
    pressureMap() {
      const vm = this;
      const pressureMap = d3.select('.c-pressure-map')
        .append('svg')
        .attr({
          width: `${36 * 16 + 2}px`,
          height: `${36 * 16 + 2}px`,
        });
      const row = pressureMap.selectAll('.row')
        .data(vm.pressureMapData)
        .enter()
        .append('g')
        .attr('class', 'row');

      row.selectAll('.square')
        .data(d => d)
        .enter().append('rect')
        .attr('class', 'square')
        .attr('x', d => d.x)
        .attr('y', d => d.y)
        .attr('width', d => d.width)
        .attr('height', d => d.height)
        .style('fill', 'transparent')
        .style('stroke', 'rgba(0, 0, 0, 0.05)');
    },
    pressureMapData() {
      const data = [];
      let xpos = 1;
      let ypos = 1;
      const width = 36;
      const height = 36;
      for (let row = 0; row < 16; row++) {
        data.push([]);
        for (let col = 0; col < 16; col++) {
          data[row].push({
            x: xpos,
            y: ypos,
            width: width,
            height: height,
          });
          xpos += width;
        }
        xpos = 1;
        ypos += height;
      }
      return data;
    },
    getStatus() {
      const rectAll = document.querySelectorAll('rect');
      rectAll.forEach((rect) => {
        // rect.classList.add(`rect${id}`);
        // rect.style.transition = 'fill 0.75s ease-in-out';
        rect.dataset.pressure = Math.floor(Math.random() * 100);
        if (rect.dataset.pressure >= 75) {
          rect.style.fill = 'rgba(255, 0, 0, 0.1)';
        } else if (rect.dataset.pressure >= 50 && rect.dataset.pressure <= 75) {
          rect.style.fill = 'rgba(255, 165, 0, 0.2)';
        } else if (rect.dataset.pressure >= 25 && rect.dataset.pressure <= 50) {
          rect.style.fill = 'rgba(0, 0, 255, 0.1)';
        } else {
          rect.style.fill = 'rgba(0, 128, 0, 0.1)';
        }
      });
    },
  },
  mounted() {
    this.pressureMap();
    this.getStatus();
    setInterval(() => this.getStatus(), 1750);
  },
};
</script>

